'use client'

import { useState, useEffect } from 'react'

// User roles as defined in the video transcript
type UserRole = 'Account Admin' | 'Account User' | 'Manager' | 'Sales Rep' | 'Support Agent' | 'Custom'

interface Permission {
  id: string
  name: string
  description: string
  category: string
}

interface RolePermissions {
  role: UserRole
  permissions: string[]
}

interface StaffMember {
  id: string
  firstName: string
  lastName: string
  email: string
  phone: string
  role: UserRole
  status: 'active' | 'pending' | 'inactive'
  avatar: string
  department: string
  customPermissions: string[]
  lastActive: string
  createdAt: string
  invitedBy: string
}

